import React from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { createQuestion } from "../actions";

class NewQuiz extends React.Component {
  constructor() {
    super();
    this.state = {
      question: null,
      answer: null,
      A: null,
      B: null,
      C: null,
      D: null
    };
  }

  onChange = ({ target: { name, value } }) => {
    this.setState({ [name]: value }, () => this.checkProgress());
  };

  createQuiz = event => {
    event.preventDefault();
    this.props.dispatch(
      createQuestion(
        this.props.topic,
        this.state.question,
        this.state.A,
        this.state.B,
        this.state.C,
        this.state.D,
        this.state.answer,
        this.props.history
      )
    );
  };
  checkProgress = () => {
    if (
      this.state.question &&
      !this.state.A &&
      !this.state.B &&
      !this.state.C &&
      !this.state.D &&
      !this.state.answer
    ) {
      return "filled filled1";
    } else if (
      this.state.question &&
      this.state.A &&
      !this.state.B &&
      !this.state.C &&
      !this.state.D &&
      !this.state.answer
    ) {
      return "filled filled2";
    } else if (
      this.state.question &&
      this.state.A &&
      this.state.B &&
      !this.state.C &&
      !this.state.D &&
      !this.state.answer
    ) {
      return " filled filled3";
    } else if (
      this.state.question &&
      this.state.A &&
      this.state.B &&
      this.state.C &&
      !this.state.D &&
      !this.state.answer
    ) {
      return "filled filled4";
    } else if (
      this.state.question &&
      this.state.A &&
      this.state.B &&
      this.state.C &&
      this.state.D &&
      !this.state.answer
    ) {
      return "filled filled5";
    } else if (
      this.state.question &&
      this.state.A &&
      this.state.B &&
      this.state.C &&
      this.state.D &&
      this.state.answer
    ) {
      return "filled filled6";
    } else {
      return "filled";
    }
  };
  render() {
    return (
      <>
        <div className="signup_container">
          <div className="arrow_container">
            <Link to="/quizsets" className="back_arrow">
              <p>â†</p>
            </Link>
            <p className="signup">Question</p>
          </div>
          <p className="question_no">
            {this.props.quizset && this.props.quizset.questions.length + 1}
          </p>
          <p
            className={
              this.state.question &&
              this.state.A &&
              this.state.B &&
              this.state.C &&
              this.state.D &&
              this.state.answer
                ? "tick tick2"
                : "tick"
            }
          >
            âœ“
          </p>
          <div className="progress_container_question">
            <p className={this.checkProgress()}></p>
          </div>
          <form onSubmit={this.createQuiz} className="new_question_form">
            <div class="control">
              <textarea
                className="textarea_question"
                name="question"
                placeholder={`Type the question for ${this.props.quizset &&
                  this.props.quizset.topic}`}
                onChange={this.onChange}
                value={this.state.question}
              ></textarea>
            </div>
            <label class="label_options">Options</label>

            <input
              className="input_question is-primary"
              type="text"
              name="A"
              onChange={this.onChange}
              value={this.state.A}
              placeholder="A"
            />
            <input
              className="input_question  is-primary"
              type="text"
              name="B"
              onChange={this.onChange}
              value={this.state.B}
              placeholder="B"
            />
            <input
              className="input_question  is-primary"
              type="text"
              name="C"
              onChange={this.onChange}
              value={this.state.C}
              placeholder="C"
            />
            <input
              className="input_question  is-primary"
              type="text"
              name="D"
              onChange={this.onChange}
              value={this.state.D}
              placeholder="D"
            />
            <select
              className="select_question"
              name="answer"
              onChange={this.onChange}
            >
              <option selected="true" disabled="disabled">
                Select your answer
              </option>
              <option>{this.state.A}</option>
              <option>{this.state.B}</option>
              <option>{this.state.C}</option>
              <option>{this.state.D}</option>
            </select>
            <button className="button_signup is-success">Create</button>
          </form>
        </div>
      </>
    );
  }
}
function mapStateToProps({ quizset }) {
  return {
    topic: quizset.topic,
    quizset: quizset.quizset
  };
}

export default connect(mapStateToProps)(withRouter(NewQuiz));
